

























import { Component, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";
const Auth = namespace("Auth");

@Component
export default class Profile extends Vue {
    
    @Auth.State("user")
    private currentUser!: any;

    mounted() {
        if(!this.currentUser) {
            this.$router.push("/login");
        }
    }
}

